.container {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}

@media screen and (max-width: 36rem) {
  .container {
    max-width: 31.875rem;
  }
}

@media screen and (max-width: 48rem) {
  .container {
    max-width: 43.125rem;
  }
}

@media screen and (max-width: 62rem) {
  .container {
    max-width: 58.125rem;
  }
}

@media screen and (max-width: 75rem) {
  .container {
    max-width: 69.375rem;
  }
}

body, div, dl, dt, dd, ul, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img, abbr {
  border: 0;
}

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption, th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

body,
html {
  font-family: "Exo 2', sans-serif";
  font-size: 100%;
  height: 100%;
  background-color: ghostwhite;
}

body {
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: 1 0 auto;
}

footer {
  flex: 0 0 auto;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

.container {
  width: 100%;
  box-sizing: border-box;
}

@font-face {
  font-family: "Exo 2', sans-serif";
  font-weight: normal;
  src: url("../fonts/Exo2-Regular.ttf");
}

.header {
  padding: 1.875rem 0 1.5625rem;
}

.header > p:first-child {
  font-size: 0.875em;
  font-weight: 600;
  text-align: center;
}

.section-hello .section-content {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding-top: 1.25rem;
  box-sizing: border-box;
}

.section-hello .col {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 0.9375rem;
  margin-right: 0.9375rem;
  width: calc(33.33333% - 1.875rem);
  word-wrap: break-word;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}

@media screen and (max-width: 48rem) {
  .section-hello .col {
    width: calc(50% - 1.875rem);
  }
}

@media screen and (max-width: 36rem) {
  .section-hello .col {
    width: calc(100% - 1.875rem);
  }
}

@media screen and (max-width: 36rem) {
  .section-hello .col {
    margin-top: 0.9375rem;
  }
  .section-hello .col:first-child {
    margin-top: 0;
  }
}

.section-hello .col:nth-child(1) {
  height: 15.625rem;
  background-color: cadetblue;
}

.section-hello .col:nth-child(2) {
  height: 20rem;
  background-color: moccasin;
}

.section-hello .col:nth-child(3) {
  height: 10rem;
  background-color: darksalmon;
}

.section-hello .col-txt {
  margin-top: 0.75rem;
  margin-bottom: 0.5rem;
}

.section-hello > p:first-child {
  font-size: 1.375em;
  font-weight: 600;
  margin: 1.25rem 0 1.25rem;
  text-align: center;
}

.footer > p:first-child {
  font-size: 0.875em;
  font-weight: 600;
  padding: 1.375rem 0 0.75rem;
  text-align: center;
}
